.header .get-in-touch-btn{
    display: none;
    @include breakpoint-lg{
        display: flex;
    }
}

.header .call-btn{
    display: flex;
    @include breakpoint-lg{
        display: none;
    }
}


.overlay{
    position: fixed;
    inset: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.6);
    -webkit-backdrop-filter: blur(6px);
    backdrop-filter: blur(6px);
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    @include breakpoint-lg{
        display: none;
    }
    &.active{
        opacity: 1;
        visibility: visible;
    }
}

.hamburgur{
    .menu-line{
        @include dimensions(rem(64),rem(3));
        background: $clr-primary;
        display: block;
        margin-bottom: 5px;
        &:nth-child(2){
            width: 3rem;
        }
        &:last-child{
            width: 2rem;
        } 
    }
    // &--mobile{
    //     display: block;
    //     @include breakpoint-lg{
    //         display: none;
    //     }
    // }
    &:hover{
        cursor: pointer;
    }
}